<template>
    <transition name="trans">
    <div v-if="isList" class="list">
        <router-link to="/Exp/01">
            <a href="" @click="onTrans" class="list_wrap">
                <h2>
                    <span class="outline">Scene</span>
                    <span class="fill">Scene</span>
                </h2>
                <div class="idx">01</div>
            </a>
        </router-link>
    </div>
    </transition>

    <transition name="trans">
    <div v-if="isList" class="list">
        <router-link to="/Exp/02">
            <a href="" @click="onTrans" class="list_wrap">
                <h2>
                    <span class="outline">Animation</span>
                    <span class="fill">Animation</span>
                </h2>
                <div class="idx">02</div>
            </a>
        </router-link>
    </div>
    </transition>

    <transition name="trans">
    <div v-if="isList" class="list">
        <router-link to="/Exp/03">
            <a href="" @click="onTrans" class="list_wrap">
                <h2>
                    <span class="outline">Create</span>
                    <span class="fill">Create</span>
                </h2>
                <div class="idx">03</div>
            </a>
        </router-link>
    </div>
    </transition>

    <transition name="trans">
    <div v-if="isList" class="list">
        <router-link to="/Exp/04">
                <a href="" @click="onTrans" class="list_wrap">
                <h2>
                    <span class="outline">Sheep</span>
                    <span class="fill">Sheep</span>
                </h2>
                <div class="idx">04</div>
            </a>
        </router-link>
    </div>
    </transition>

    <transition name="trans">
    <div v-if="isIndex" class="control_bar">
      <a href="#" @click="toIndex">INDEX ➟</a>
    </div>
    </transition>
</template>

<script>
export default {
  name: 'HomeView',
  data () {
    return {
      isList: true,
      isIndex: false
    }
  },
  methods: {
    onTrans () {
      if (this.isList) {
        this.isList = false
        this.isIndex = true
      }
    },
    toIndex () {
      this.isIndex = false
      this.isList = true
    }
  }
}
</script>

<style scoped lang="scss">
.list a{
    color: #fff;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    font-family: 'FT88', sans-serif;
    position: relative;
    z-index: 6;
    h2{
        position: relative;
        span{
            font-size: 6vw;
            &.outline{
                position: absolute;
                -webkit-text-stroke: 1px #fff;
                text-stroke: 1px #fff;
                color: transparent;
                z-index: -1;
            }
            &.fill{
                transition: 0.5s;
                &:hover{
                    opacity: 0;
                }
            }
        }
    }
}
.control_bar{
      position: fixed;
      top: 0;
      right: 5vw;
      width: 400px;
      height: 70px;
      line-height: 70px;
      background-color: transparent;
      z-index: 50;
      text-align: right;
      a{
        font-family: 'FT88', sans-serif;
        color: #fff;
        margin-left: 5vw;
        font-size: 3.5vw;
      }
}
.trans-enter-active, .trans-leave-active{
    transition: 0.5s;
  }
  .trans-enter-from, .trans-leave-to{
    opacity: 0;
    transform: translateX(50px);
  }
</style>
